import * as React from 'react';
import { Controller } from '@ali/4ever-cangjie';
import { AddFontSizeButton, ReduceFontSizeButton } from '@ali/we-toolbar';
import { PluginRoles } from '@ali/4ever-bamboo';
import { adjustSz } from '../actions';
import { SHORTCUT } from '../utils/getTipsByType';

export interface SzButtonProps {
  controller: Controller;
  locale: Record<string, string>;
}

export function SzBiggerButton(props: SzButtonProps) {
  const { controller, locale, ...rest } = props;

  const handleClick = React.useCallback(() => {
    controller.run('onAction', adjustSz(true));
  }, [controller]);
  const isDisabled = controller.query('isSelectionInLinkCard');
  return (
    <AddFontSizeButton
      {...rest}
      onClick={handleClick}
      shortcutText={SHORTCUT.bigger}
      disabled={isDisabled}
      role={PluginRoles.szIncrease}
    />
  );
}

export function SzSmallerButton(props: SzButtonProps) {
  const { controller, locale, ...rest } = props;

  const handleClick = React.useCallback(() => {
    controller.run('onAction', adjustSz(false));
  }, [controller]);
  const isDisabled = controller.query('isSelectionInLinkCard');
  return (
    <ReduceFontSizeButton
      {...rest}
      onClick={handleClick}
      shortcutText={SHORTCUT.smaller}
      disabled={isDisabled}
      role={PluginRoles.szDecrease}
    />
  );
}
